import { request as __request } from '../core/request';
export class PartnerService {
    /**
     * @param requestBody
     * @returns any OK
     * @throws ApiError
     */
    static add(requestBody) {
        return __request({
            method: 'POST',
            path: `/api/partner/add`,
            body: requestBody,
            mediaType: 'application/json',
        });
    }
    /**
     * @param partnerId
     * @returns PartnerDto OK
     * @throws ApiError
     */
    static get(partnerId) {
        return __request({
            method: 'GET',
            path: `/api/partner/get/${partnerId}`,
        });
    }
    /**
     * @returns PartnerReferenceDto OK
     * @throws ApiError
     */
    static list() {
        return __request({
            method: 'GET',
            path: `/api/partner/list`,
        });
    }
    /**
     * @param requestBody
     * @returns any OK
     * @throws ApiError
     */
    static update(requestBody) {
        return __request({
            method: 'POST',
            path: `/api/partner/update`,
            body: requestBody,
            mediaType: 'application/json',
        });
    }
}
