import { request as __request } from '../core/request';
export class ClientDocumentService {
    /**
     * @param id
     * @returns any OK
     * @throws ApiError
     */
    static delete(id) {
        return __request({
            method: 'DELETE',
            path: `/api/client-document/delete/${id}`,
        });
    }
    /**
     * @param id
     * @returns FileDto OK
     * @throws ApiError
     */
    static get(id) {
        return __request({
            method: 'GET',
            path: `/api/client-document/get/${id}`,
        });
    }
    /**
     * @param formData
     * @returns ClientDocumentDto OK
     * @throws ApiError
     */
    static upload(formData) {
        return __request({
            method: 'POST',
            path: `/api/client-document/upload`,
            formData: formData,
            mediaType: 'multipart/form-data',
        });
    }
}
