import { request as __request } from '../core/request';
export class TemplateService {
    /**
     * @param formData
     * @returns any OK
     * @throws ApiError
     */
    static add(formData) {
        return __request({
            method: 'POST',
            path: `/api/template/add`,
            formData: formData,
            mediaType: 'multipart/form-data',
        });
    }
    /**
     * @param templateId
     * @returns any OK
     * @throws ApiError
     */
    static delete(templateId) {
        return __request({
            method: 'DELETE',
            path: `/api/template/delete/${templateId}`,
        });
    }
    /**
     * @param templateId
     * @returns FileDto OK
     * @throws ApiError
     */
    static get(templateId) {
        return __request({
            method: 'GET',
            path: `/api/template/get/${templateId}`,
        });
    }
    /**
     * @returns TemplateDto OK
     * @throws ApiError
     */
    static list() {
        return __request({
            method: 'GET',
            path: `/api/template/list`,
        });
    }
}
