import { request as __request } from '../core/request';
export class ClientService {
    /**
     * @param requestBody
     * @returns any OK
     * @throws ApiError
     */
    static add(requestBody) {
        return __request({
            method: 'POST',
            path: `/api/client/add`,
            body: requestBody,
            mediaType: 'application/json',
        });
    }
    /**
     * @param clientId
     * @returns any OK
     * @throws ApiError
     */
    static delete(clientId) {
        return __request({
            method: 'DELETE',
            path: `/api/client/delete/${clientId}`,
        });
    }
    /**
     * @param id
     * @returns ClientDto OK
     * @throws ApiError
     */
    static get(id) {
        return __request({
            method: 'GET',
            path: `/api/client/get/${id}`,
        });
    }
    /**
     * @returns ClientDto OK
     * @throws ApiError
     */
    static list() {
        return __request({
            method: 'GET',
            path: `/api/client/list`,
        });
    }
    /**
     * @returns ClientDto OK
     * @throws ApiError
     */
    static listAll() {
        return __request({
            method: 'GET',
            path: `/api/client/list-all`,
        });
    }
    /**
     * @param requestBody
     * @returns any OK
     * @throws ApiError
     */
    static update(requestBody) {
        return __request({
            method: 'POST',
            path: `/api/client/update`,
            body: requestBody,
            mediaType: 'application/json',
        });
    }
}
