import { request as __request } from '../core/request';
export class ProofOfIdentityService {
    /**
     * @param formData
     * @returns ProofOfIdentityDto OK
     * @throws ApiError
     */
    static add(formData) {
        return __request({
            method: 'POST',
            path: `/api/proof-of-identity/add`,
            formData: formData,
            mediaType: 'multipart/form-data',
        });
    }
    /**
     * @param id
     * @returns any OK
     * @throws ApiError
     */
    static delete(id) {
        return __request({
            method: 'DELETE',
            path: `/api/proof-of-identity/delete/${id}`,
        });
    }
    /**
     * @param id
     * @returns FileDto OK
     * @throws ApiError
     */
    static get(id) {
        return __request({
            method: 'GET',
            path: `/api/proof-of-identity/get/${id}`,
        });
    }
}
