import { request as __request } from '../core/request';
export class ClientContractService {
    /**
     * @param requestBody
     * @returns ClientContractDto OK
     * @throws ApiError
     */
    static add(requestBody) {
        return __request({
            method: 'POST',
            path: `/api/client-contract/add`,
            body: requestBody,
            mediaType: 'application/json',
        });
    }
    /**
     * @param id
     * @returns any OK
     * @throws ApiError
     */
    static delete(id) {
        return __request({
            method: 'DELETE',
            path: `/api/client-contract/delete/${id}`,
        });
    }
    /**
     * @param contractId
     * @returns FileDto OK
     * @throws ApiError
     */
    static get(contractId) {
        return __request({
            method: 'GET',
            path: `/api/client-contract/get/${contractId}`,
        });
    }
    /**
     * @param requestBody
     * @returns any OK
     * @throws ApiError
     */
    static update(requestBody) {
        return __request({
            method: 'POST',
            path: `/api/client-contract/update`,
            body: requestBody,
            mediaType: 'application/json',
        });
    }
    /**
     * @param formData
     * @returns any OK
     * @throws ApiError
     */
    static upload(formData) {
        return __request({
            method: 'POST',
            path: `/api/client-contract/upload`,
            formData: formData,
            mediaType: 'multipart/form-data',
        });
    }
}
