import { request as __request } from '../core/request';
export class EmployeeService {
    /**
     * @param requestBody
     * @returns any OK
     * @throws ApiError
     */
    static add(requestBody) {
        return __request({
            method: 'POST',
            path: `/api/employee/add`,
            body: requestBody,
            mediaType: 'application/json',
        });
    }
    /**
     * @param id
     * @returns EmployeeDto OK
     * @throws ApiError
     */
    static get(id) {
        return __request({
            method: 'GET',
            path: `/api/employee/get/${id}`,
        });
    }
    /**
     * @returns EmployeeDto OK
     * @throws ApiError
     */
    static list() {
        return __request({
            method: 'GET',
            path: `/api/employee/list`,
        });
    }
    /**
     * @param requestBody
     * @returns any OK
     * @throws ApiError
     */
    static update(requestBody) {
        return __request({
            method: 'POST',
            path: `/api/employee/update`,
            body: requestBody,
            mediaType: 'application/json',
        });
    }
}
